.page-not-found {
  max-width: 500px;
  padding: 40px;
  text-align: center;
  top: 50%;
  left: 50%;
  position: absolute;
  transform: translate(-50%, -50%);
}

.page-not-found .title {
  font-size: 50px;
  letter-spacing: 10px;
  margin-bottom: 10px;
}

.page-not-found .desc {
  font-size: 20px;
  margin-bottom: 20px;
}

.go-back-btn {
  min-width: 160px;
}
