.root {
  display: flex;
  flex-wrap: wrap;
  position: absolute;
  top: 25%;
  left: 28%;
  width: auto;
  justify-content: space-around;
  overflow: hidden;
}

.gridList {
  width: 100%;
  height: 100%;
}

.portrait {
  width: 100%;
  height: 100%;
}

.art {
  position: relative;
  display: block;
  flex: 1 1 0px;
  transition: transform 500ms;
}

.grid {
  transition: transform 500ms;
}

.root .grid:focus,
.root .grid:hover {
  transform: scale(1.2);
  z-index: 1;
  cursor: pointer;
}
