.profile-container {
  width: 25%;
  left: 2%;
  top: 10%;
  position: absolute;
  padding: 2%;
  background-color: aliceblue;
}

.profile-container .name {
  margin-top: 5%;
  font-size: 1.5rem;
  font-weight: 600;
}

.profile-container .desc {
  margin-top: 5%;
  font-size: 1rem;
}

.profile-container .country {
  margin-top: 5%;
  font-size: 1rem;
  font-weight: 500;
}

.text {
  color: black;
  text-align: center;
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
}

.profile-container .image {
  width: 60%;
  height: 60%;
  display: block;
  margin: auto;
}

.profile-container .contact-me-head {
  margin-top: 15%;
  font-size: 1rem;
  font-weight: bold;
}

.profile-container .contact-me-desc {
  font-size: 1rem;
}

.profile-container .copyright {
  margin-top: 25%;
  margin-bottom: -12.5%;
  padding-bottom: 10%;
  font-size: 0.85rem;
}

.profile-container .divider {
  margin-top: 15%;
}
